body {
  height: 100%;
  margin: 0;
  font-family: "Muli", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1rem;
  font-weight: 400;
  color: #212529;
  text-align: center;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

.root{
  flex: 1 0 auto;
  background-color: #22262a;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

html {
  height: 100%;
}

h4 {
  font-size: 24px;
  font-weight: bold;
}

.small-text {
  font-size: 12px;
}

.medium-text {
  font-size: 16px;
}

.large-text {
  font-size: 24px;
}

.huge-text {
  font-size: 44px;
}

@media (min-width: 1700px) {
  h4 {
    font-size: 0.705vw;
    font-weight: bold;
  }

  .small-text {
    font-size: 0.706vw;
  }

  .medium-text {
    font-size: 0.941vw;
  }

  .large-text {
    font-size: 1.411vw;
  }

  .huge-text {
    font-size: 2.587vw;
  }
}


p {
  margin:0px;
}

.seo-title{
  display: none;
}

.about {
  display: inline-block;
  margin: 36.5px;
  height: 75.2px;
}

.title {
  font-size: 44px;
  font-weight: bold;
}

.subtitle {
  font-size: 16px;
}

.about-content {
  flex: 1 0 auto;
  background-color: #22262a;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.animation{
  animation: transitionIn 0.75s;
}

.projects-content {
  flex: auto;
  background-color: #22262a;
  color: #fff;
}

.view-projects-button, .error-button {
  display: inline-block;
  text-decoration: none;
  padding: 10px;
  color: white;
  border: solid 2px;
  border-color: white;
  cursor: pointer;
  background-color: #22262a;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
}

  .view-projects-button:hover, .error-button:hover {
    background-color: #fff;
    color: #22262a;
  }

.view-projects-button-container {
  justify-content: center;
  align-content: center;
  align-items: center;
}

.footer {
  display: none;
  background-color: #22262a;
  color: white;
  flex-shrink: 0;
}

.copyright {
  font-size: 12px;
  margin: 5px;
}

.filter {
  margin: 5px;
  max-width: 500px;
  display: inline-block;
  animation: transitionIn 0.75s;
}

input[type=checkbox] {
  display: none;
}

label {
  list-style: none;
  display: inline-block;
  font-weight: bold;
  padding: 3px 5px;
  font-size: small;
  margin: 2.5px;
  border: 2px solid;
  cursor: pointer;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: all 0.3s ease-in-out;
}

  label:hover {
    background-color: #fff;
    color: #000000;
  }

input[type=checkbox]:checked + label {
  padding: 10px;
}

.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}

.transform {
  animation: transitionIn2 0.75s;
}

.transform-active {
  animation: transitionIn 0.75s;
}

.project-card {
  display: flex;
  flex-direction: column;
  width: 350px;
  height: auto;
  margin-left: .5rem;
  margin-right: .5rem;
  margin-bottom: 1em;
  color: #fff;
  background-color: #343a41;
  box-shadow: 8px 4px 8px 4px rgba(0,0,0,0.2);
  transition: all 0.3s ease-in-out;
}

.project-card-details {
  padding: 8px;
  text-align: left;
}

.project-details {
  overflow-y: auto;
  height: 100px;
}

.project-image-container {
  height: 200px;
  overflow: hidden;
}

.project-image {
  max-width: 350px;
  min-height: 200px;
  height: auto;
}

.project-card:hover .project-card-basic-info, .project-card:active .project-card-basic-info {
  color: #fff;
}

.project-card:hover, .project-card:active {
  transform: scale(1.03);
}

  .project-card:hover .project-card-details, .project-card:active .project-card-details {
    opacity: 1;
  }

.project-title {
  font-size: 20px;
  font-weight: bold;
}

.project-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}

.project-button {
  margin: 8px;
  margin-bottom: 0px;
  padding: 3px 5px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  transition: all 0.3s ease-in-out;
  border: none;
  color: white;
  background-color: #495057;
}

  .project-button:hover {
    background-color: #fff;
    color: #000000;
  }

.project-icons .project-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 3.5rem;
  width: 3.5rem;
  background-color: #495057;
  color: #fff;
  font-size: 1.5rem;
  margin-right: 1.5rem;
  margin-bottom: 1em;
}

  .project-icons .project-icon:last-child {
    margin-right: 0;
  }

  .project-icons .project-icon:hover {
    background-color: #4a38bd;
  }

.social-icons .social-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 16px;
  margin: 16px;
  margin-left: 4px;
  margin-right: 4px;
  padding: 4px;
  transition: all 0.3s ease-in-out;
}

  .social-icons .social-icon:hover {
    background-color: #fff;
    color: #000000
  }

.skills {
  color: #fff;
  list-style: none;
  min-height: 55px;
}

.skill {
  display: inline-block;
  font-weight: bold;
  padding: 3px 5px;
  font-size: 10px;
  margin: 2.5px;
  border: 2px solid;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-thumb {
  background-color: #495057;
}

::-webkit-scrollbar-track {
  background-color: #000000;
}

@keyframes transitionIn {
  from {
    opacity: 0;
    transform: translateY(-10px)
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes transitionIn2 {
  from {
    opacity: 0;
    transform: translateY(-10px)
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (min-width: 768px) {
  .projects-content {
    padding-top: 5.229vw;
  }
}

@media (min-width: 992px) {
  .projects-content {
    padding-left: 7.843vw;
    padding-right: 7.843vw;
  }
}

@media (min-width: 1700px) {
  .about {
    display: inline-block;
    margin: 2.147vw;
    height: 4.424vw;
  }

  .title {
    font-size: 2.588vw;
  }

  .subtitle {
    font-size: 0.941vw;
  }

  .view-projects-button {
    padding: 0.651vw;
    color: white;
    border: solid 0.130vw;
    font-size: 0.868vw;
  }

  .project-card {
    width: 22.876vw;
    margin: 0.294vw;
    box-shadow: 0.523vw 0.261vw 0.523vw 0.261vw rgba(0,0,0,0.2);
  }

  .project-image-container {
    height: 13.072vw;
  }

  .project-image {
    max-width: 100%;
    min-height: 13.072vw;
  }

  .project-title {
    font-size: 1.176vw;
  }

  .project-button {
    margin: 0.523vw;
    margin-bottom: 0px;
    padding: 0.176vw 0.294vw;
    font-size: 1.045vw;
  }

  .project-details {
    height: 6.536vw;
  }

  .project-card-details {
    padding: 0.523vw;
  }

  .skills {
    min-height: 3.595vw;
  }

  .skill {
    padding: 0.176vw 0.294vw;
    font-size: 0.654vw;
    margin: 0.147vw;
    border: 0.131vw solid;
  }

  .filter {
    margin: 0.294vw;
    max-width: 32.680vw;
  }

  .social-icons .social-icon {
    font-size: 1.042vw;
    margin: 0.915vw;
    margin-left: 0.260vw;
    margin-right: 0.260vw;
    padding: 0.260vw;
  }

  .copyright {
    margin: 0.294vw;
    font-size: 0.784vw;
  }

  label {
    padding: 0.176vw 0.294vw;
    font-size: 0.850vw;
    margin: 0.147vw;
    border: 2px solid;
    box-shadow: 0 0.261vw 0.522vw 0 rgba(0,0,0,0.2);
    margin-left: 0.08vw;
  }

  ::-webkit-scrollbar {
    width: 0.392vw;
  }
}

